import { CLDRFramework, LocaleMatcher } from "@phensley/cldr";
import pkg from "../../package.json";

// Get exact version of @phensley/cldr dependency
const version = pkg.dependencies["@phensley/cldr"];

const packurl = `https://cdn.jsdelivr.net/npm/@phensley/cldr@${version}/packs`;

// Fetch the language resource pack for this version.
const asyncLoader = language =>
  fetch(`${packurl}/${language}.json`)
    .then(r => r.json())
    .catch(err => console.log(err));

export const framework = new CLDRFramework({
  loader: locale => require(`@phensley/cldr/packs/${locale}.json`),
  asyncLoader,
  packCacheSize: 8
});

const allLocales = CLDRFramework.availableLocales();

const supported = allLocales
  // place the default locale at the front of the supported list
  .sort(l => (l.tag.expanded() === "en-Latn-US" ? -1 : 1))
  .map(l => l.id);

export const localeMatcher = new LocaleMatcher(supported);
