import * as React from 'react';
import './App.css';
import DateFormattingContainer from './components/DateFormattingContainer';
import { customizeMoment } from '@sqs/i18n-ui';
import { framework, localeMatcher } from "./cldr";
// Import any types used in your example
import { CLDR, CLDRFramework, Decimal, DecimalConstants } from "@phensley/cldr";

type State = {
  formattingLocale: string,
  cldr: any
};

type Props = any;

class App extends React.Component<Props, State> {
  public state: State = {
    formattingLocale: 'en-US',
    cldr: undefined
  };

  componentDidMount(){
    this.setLocale(this.state.formattingLocale);
  }

  private setLocale(formattingLocale: string){
    // Update moments locale via customizeMoment
    customizeMoment(formattingLocale);
    this.setState({
      formattingLocale
    });
    // Set cldr-engine locale
    const match = localeMatcher.match(formattingLocale.split('-')[0]);
    framework.getAsync(match.locale).then(cldr => this.setState({ cldr }));
  }

  private onChangeLocale = (event: React.FormEvent<HTMLSelectElement>) => {
    const formattingLocale = event.currentTarget.value;
    this.setLocale(formattingLocale);
  }

  public render() {
    const { cldr } = this.state;
    return (
      <div className="App">
        <header className="App-header">
          <h1>Moment vs CLDR.Calendar</h1>
        </header>
        <div>
          <DateFormattingContainer />
        </div>
      </div>
    )
  };
}

export default App;
