import * as React from 'react';
import moment from 'moment';
import classnames from 'classnames';

import { customizeMoment } from '@sqs/i18n-ui';
import { framework } from "../../cldr";

const LOCALES = ['en-US', 'es-419', 'de-DE', 'fr-FR', 'it-IT', 'pt-BR'];
const nowTimestamp = moment().unix();
// This object maps Moments localized tokens to the compatible CLDR.Calendar skeleton/token
const localizedTokenMap: {[key: string]: string} = {
  'LLL':'yMMMMd',
  'lllLT':'yMMMd hms',
  'L hh:mm:ss':'yMd hms',
  'll':'MMMd',
  'llddd':'MMMEd',
  'llll':'MMMEdy',
  'LLLL':'MMMMEEEEdy',
  'LT':'hm',
  'LLLLT':'yMMMMd h:mm a',
  'LLTS':'yMd hms'
};
// Custom moment token tokens that map 
const customTokenMap: {[key: string]: string} = {
  'MM-YYYY':'MM-y',
  'MM-DD-YYYY':'MM-d-y',
  'MMMM Do YYYY':'MMMM d y',
  'MMMM':'MMMM',
  'MMMM YYYY':'MMMM y',
  'MMM':'MMM',
  'A':'a',
  'ddd':'E',
  'h':'h',
  'mm':'m',
  'h:mma':'h:mma',
  'ha': 'ha',
  'YYYY':'y',
  'MMM DD, YYYY hh:mm A':'MMM d, y hh:mm a',
  'MM/YY':'MM/YY',
  'D':'d',
}
const renderLocalizedTable = (locale: string) => {
  const nowMoment = moment(nowTimestamp).clone();
  // Get CLDR framework by locale
  const cldr = framework.get(locale.split('-')[0]);
  const cldrDate = { 
    date: nowMoment.toDate(),
    zoneId: "America/New_York"
  };
  const getMomentFormattingToken = (tokenKey: any) => {
    const localeToken = moment.localeData().longDateFormat(tokenKey);
    if(tokenKey in moment.localeData().longDateFormat){
      return moment.localeData().longDateFormat(tokenKey);
    }
    return localeToken || tokenKey;
  };
  return (
    <div className="tableContainer">
      <table>
        <tbody>
          <tr>
            <th>Match</th>
            <th>Moment Token</th>
            <th>CLDR.Calendar Token</th>
            <th>Moment Output</th>
            <th>CLDR.Calendar Output</th>
          </tr>
        {Object.keys(localizedTokenMap).map((tokenKey: string)=> {
          const momentValue = nowMoment.format(getMomentFormattingToken(tokenKey));
          const cldrValue = cldr.Calendars.formatDate(cldrDate, { 
              context: 'standalone', 
              skeleton: localizedTokenMap[tokenKey]
            });
          const doValuesMatch = momentValue == cldrValue;
          return (
            <tr key={tokenKey} className={classnames({mismatch: !doValuesMatch})}>
              <td>{doValuesMatch ? <span>&#9989;</span> :  <span>&#10060;</span>}</td>
              <td>{tokenKey}</td>
              <td>{localizedTokenMap[tokenKey]}</td>
              <td>{momentValue}</td>
              <td>{cldrValue}</td>
            </tr>
          );
        })}
        </tbody>
      </table>
    </div>
  );
};

const renderCustomTable = (locale: string) => {
  const nowMoment = moment(nowTimestamp).clone();
  // Get CLDR framework by locale
  const cldr = framework.get(locale.split('-')[0]);
  const cldrDate = { 
    date: nowMoment.toDate(),
    zoneId: "America/New_York"
  };
  const getMomentFormattingToken = (tokenKey: any) => {
    const localeToken = moment.localeData().longDateFormat(tokenKey);
    if(tokenKey in moment.localeData().longDateFormat){
      return moment.localeData().longDateFormat(tokenKey);
    }
    return localeToken || tokenKey;
  };
  return (
    <div className="tableContainer">
      <table>
        <tbody>
          <tr>
            <th>Match</th>
            <th>Moment Token</th>
            <th>CLDR.Calendar Token</th>
            <th>Moment Output</th>
            <th>CLDR.Calendar Output</th>
          </tr>
        {Object.keys(customTokenMap).map((tokenKey: string)=> {
          const momentValue = nowMoment.format(getMomentFormattingToken(tokenKey));
          const cldrValue = cldr.Calendars.formatDateRaw(cldrDate, { 
              pattern: customTokenMap[tokenKey]
            });
          const doValuesMatch = momentValue == cldrValue;
          return (
            <tr key={tokenKey} className={classnames({mismatch: !doValuesMatch})}>
              <td>{doValuesMatch ? <span>&#9989;</span> :  <span>&#10060;</span>}</td>
              <td>{tokenKey}</td>
              <td>{customTokenMap[tokenKey]}</td>
              <td>{momentValue}</td>
              <td>{cldrValue}</td>
            </tr>
          );
        })}
        </tbody>
      </table>
    </div>
  );
};

const DateFormattingContainer = () => (
  <div>
    <h2>Date Formatting Tokens </h2>
      <h3>Function signatures</h3>
      <pre>
        <code>
          <span>// Momentjs</span>
          <span>moment().format(getFormattingToken(token));</span><br/>
          <span>// CLDR.Calendar localized format</span>
          <span>{`cldr.Calendars.formatDate(date, { context: 'standalone', skeleton: token});`}</span>
          <span>// CLDR.Calendar custom format</span>
          <span>{`cldr.Calendars.formatDateRaw(date, { pattern: token});`}</span>
        </code>
      </pre>
      { LOCALES.map(locale => {
        // Customize Momentjs
        customizeMoment(locale);
        return (
          <div>
            <h3>Localized output for {locale}</h3>
            {renderLocalizedTable(locale)}
            <h3>Custom output for {locale}</h3>
            {renderCustomTable(locale)}
          </div>
        )}) 
      }
  </div>
);

export default DateFormattingContainer;